import React, { useEffect } from 'react';
import { Environment, useDefaultProgrammingStore } from '../components';
import useMeasure from 'react-use-measure';
import connectionConfig from './assets/connectionConfig';
import connectionStarter from './assets/connectionStarter';
import { ErrorBoundary } from 'react-error-boundary';
import ErrorFallback from './ErrorFallback';

// More on default export: https://storybook.js.org/docs/react/writing-stories/introduction#default-export
export default {
  title: 'Connections',
  component: Environment
};

// More on component templates: https://storybook.js.org/docs/react/writing-stories/introduction#using-args
const Template = args => {
  const {
    drawers,
    objectTypes,
    programData,
    executionData,
    drawerWidth,
    tabs,
    activeTab,
    ...otherArgs
  } = args;
  const [ref, bounds] = useMeasure();
  useEffect(() => {
    useDefaultProgrammingStore.setState({
      programSpec: {
        drawers,
        objectTypes
      },
      programData,
      executionData,
      tabs,
      activeTab
    });
  }, [drawers, objectTypes, programData, executionData, tabs, activeTab]);
  return <ErrorBoundary FallbackComponent={ErrorFallback}>
      <div ref={ref} style={{
      display: 'flex',
      height: '100vh',
      flexDirection: 'row',
      backgroundColor: '#333'
    }}>
        <Environment {...otherArgs} store={useDefaultProgrammingStore} height={bounds.height} width={bounds.width} drawerWidth={drawerWidth} />
      </div>
    </ErrorBoundary>;
};
export const Connections = Template.bind({});
// More on args: https://storybook.js.org/docs/react/writing-stories/args
Connections.args = {
  highlightColor: '#ff00ff',
  drawerWidth: 235,
  snapToGrid: true,
  ...connectionConfig,
  ...connectionStarter
};
Connections.parameters = {
  ...Connections.parameters,
  docs: {
    ...Connections.parameters?.docs,
    source: {
      originalSource: "args => {\n  const {\n    drawers,\n    objectTypes,\n    programData,\n    executionData,\n    drawerWidth,\n    tabs,\n    activeTab,\n    ...otherArgs\n  } = args;\n  const [ref, bounds] = useMeasure();\n  useEffect(() => {\n    useDefaultProgrammingStore.setState({\n      programSpec: {\n        drawers,\n        objectTypes\n      },\n      programData,\n      executionData,\n      tabs,\n      activeTab\n    });\n  }, [drawers, objectTypes, programData, executionData, tabs, activeTab]);\n  return <ErrorBoundary FallbackComponent={ErrorFallback}>\n      <div ref={ref} style={{\n      display: 'flex',\n      height: '100vh',\n      flexDirection: 'row',\n      backgroundColor: '#333'\n    }}>\n        <Environment {...otherArgs} store={useDefaultProgrammingStore} height={bounds.height} width={bounds.width} drawerWidth={drawerWidth} />\n      </div>\n    </ErrorBoundary>;\n}",
      ...Connections.parameters?.docs?.source
    }
  }
};